<template>
  <Bar v-if="finishCount" id="my-chart-id" :options="chartOptions" :data="chartData"/>
</template>

<script>
import { Bar } from "vue-chartjs";
import {
  Chart as ChartJS,
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale,
} from "chart.js";

ChartJS.register(
  Title,
  Tooltip,
  Legend,
  BarElement,
  CategoryScale,
  LinearScale
);

export default {
  name: "BarChart",
  components: { Bar },
  data() {
    return {
      chartData: {
        labels: [
          "01",
          "02",
          "03",
          "04",
          "05",
          "06",
          "07",
          "08",
          "09",
          "10",
          "11",
          "12",
          "01",
          "02",
          "03",
          "04",
          "05",
          "06",
          "07",
          "08",
          "09",
          "10",
          "11",
          "12",
        ],
        datasets: [
          {
            data: []
          },
        ],
      },
      chartOptions: {
        responsive: true,
      },
      finishCount: false,
    };
  },
  computed:{},
  mounted() {
    this.arr15();
  },
  methods: {
    arr15() {
      // 
      let array = [];

      // 
      let now = new Date();

      // 00:00
      let start = new Date(
        now.getFullYear(),
        now.getMonth(),
        now.getDate(),
        0,
        0
      );

      // 15
      let end = new Date(
        now.getFullYear(),
        now.getMonth(),
        now.getDate(),
        now.getHours(),
        Math.floor(now.getMinutes() / 15) * 15
      );

      // 15
      let interval = 15 * 60 * 1000;

      // while
      while (start <= end) {
        // HH:mm
        let timeString = start.toTimeString().slice(0, 5);

        // 
        array.push(timeString);

        // 
        start = new Date(start.getTime() + interval);
      }

      // 
      console.log(array);
      
      // yoyo
      this.chartData.labels = array;
      this.chartData.datasets[0].data=[
              15, 20, 12, 13, 8, 22, 6, 33, 29, 10, 9, 32, 40, 20, 12, 13, 8,
              22, 6, 33, 29, 10, 9, 32,
            ],
      this.finishCount = true;
    },
    // AI
    // js
    // 00:001523:45,
    // ['00:00', '00:15','00:30'...'23:45'], '23:45',
    // 10:35,'10:30',
    // 11:50, '11:45',
    // 11:59,'11:45', 
    // 
  },
};
</script>
